export default {
	props: {
		list: {
			type: Array,
			default: function() {
				return []
			}
		}
	},
	data() {
		return {
			extClass: '',
			current: 0
		};
	},
	methods: {
		tabChange(index) {
			if (index === this.current) {
				return;
			}
			this.current = index;
			this.$emit('tabChange',index)
		}
	}
};
